@import "../bower_components/bootstrap-sass/assets/stylesheets/_bootstrap.scss";

/* http://htmlbook.ru/blog/polzovatelskie-skrollbary-v-webkit */
.scrollbar{
  p{
    max-height:150px;
    overflow-y: auto;
  }
  ::-webkit-scrollbar {
    width: 12px;
  }
  ::-webkit-scrollbar-track {
    -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,0.4);
    border-radius: 10px;
  }
  ::-webkit-scrollbar-thumb {
    border-radius: 10px;
    -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,0.4);
  }
}

.mosaic{
  > .col-sm-6{
    &:nth-child(odd){
      float:left;
    }
    &:nth-child(even){
      float:right;
    }
  }
}

.checkbox{
  &.checkbox-warning {
    label{
      padding-left: 10px;
    }
    label::before{
      width:18px;
      height:18px;
      margin-left: -18px;
      background-color:transparent;
      border-color: #010101;
    }
    label::after{
      width:18px;
      height:18px;
      margin-left: -18px;
      padding-left: 2px;
      padding-top: 2px;
      font-size:13px;
    }
    input[type="checkbox"]{
      &:focus *{
        outline:none !important;
      }
      &:checked{
        + label::before{
          background-color:$orange;
          border-color: $orange;
          outline-color: $orange;
        }
      }
      &:disabled {
        + label::before{
          opacity: 0.7;
        }
      }
    }
  }
}

