    color:$gray-light;
    text-align: right;
  }
  p{
    font-size:$font-size-base;
    line-height: 1.4;
    font-family: $font-family-serif;
    margin-bottom: 9px;
  }
  time{
    display: block;
    font-size:$font-size-small;
    line-height: 1.3;
    color:$gray-light;
    margin-top: 5px;
    margin-bottom: 12px;
  }
  .well{
    padding: 15px 15px 5px;
    margin: 15px -5px;
    background-color:#eeeeee;
    box-shadow: none;
    border-radius:0px;
    background-image:none;
    background-repeat:no-repeat;

    &.well-info{
      background-color:#f0f4c3;
      border-color:transparent;
      p{
        font-size:$font-size-base;
        font-family: $font-family-serif;
        color:$text-color;
        line-height: 1.4;
      }
    }
    h2{
      font-size:$font-size-base;
      line-height: 1.1;
      color:$gray-light;
      font-style:italic;
      margin-top: 0;
      margin-bottom: 9px;
    }
    a{
      font-size:$font-size-h4;
      margin-bottom: 3px;
      line-height: 1.3;
      display: block;
    }
    p{
      font-size:$font-size-base;
      line-height: 1.1;
      color: rgba(0, 0, 0, 0.4);
      margin-bottom: 9px;
      font-family:$font-family-base;
    }
  }
