    max-width:100%;
    margin-bottom: 10px;
    display: block;
  }
  .newsPaging{
    a{
      font-size: $font-size-base;
      line-height: 1.1;
      position: relative;
      padding: 0px 35px;
      display: inline-block;
      &::before{
        content:'';
        width:24px;
        height:24px;
        position: absolute;
        top:3px;
        background-position: center;
        background-repeat: no-repeat;
      }
    }
    .prev{
      a::before{
        background-image: url('../img/icons/ic-arrow-back.svg');
        left:5px;
      }
    }
    .next{
      text-align: right;
      a::before{
        background-image: url('../img/icons/ic-arrow-forward.svg');
        left:auto;
        right:5px;
      }
    }
  }
  .social{
    border-top:1px solid $light;
    padding: 11px 0;
    line-height: 28px;
    overflow: hidden;
    margin-bottom: 15px;
    p{
      font-family:$font-family-base;
      overflow: hidden;
      margin-bottom: 0;
      span{
        display: inline-block;
        line-height: 28px;
        color: rgba(0, 0, 0, 0.4);
        font-size:$font-size-base;
        margin-right: 16px;
        float:left;
      }
    }
    a{
      &:first-child{
        margin-left: 0;
      }
      display: inline-block;
      float:left;
      width:28px;
      height:28px;
      margin:0px 6px 0px 0px;
      text-indent: -9999px;
      background-size: 100%;
      background-repeat:no-repeat;
      background-position: center;
      &.fb{
        background-image: url('../img/icons/social/fb.svg');
        &:hover{
          background-image: url('../img/icons/social/fb-hover.svg');
        }
      }
      &.vk{
        background-image: url('../img/icons/social/vk.svg');
        &:hover{
          background-image: url('../img/icons/social/vk-hover.svg');
        }
      }
      &.inst{
        background-image: url('../img/icons/social/inst.svg');
        &:hover{
          background-image: url('../img/icons/social/inst-hover.svg');
        }
      }
      &.yt{
        background-image: url('../img/icons/social/yt.svg');
        &:hover{
          background-image: url('../img/icons/social/yt-hover.svg');
        }
      }
      &.tw{
        background-image: url('../img/icons/social/tw.svg');
        &:hover{
          background-image: url('../img/icons/social/tw-hover.svg');
        }
      }
    }
  }
  .list-group{
    border-top:4px solid $light;
    border-bottom:4px solid $light;
    .list-group-item{
      font-size:$font-size-base;
      border-top:2px solid $light;
      padding-top: 20px;
      padding-bottom: 15px;
      h2{
        font-size:$font-size-base;
        color:$gray-light;
        font-style:italic;
        line-height: 1.1;
        margin-top: 0;
        margin-bottom: 8px;
      }
      a{
        display: block;
        line-height: 1.1;
        margin-bottom: 8px;
      }
      p {
        line-height: 1.3;
        font-size:$font-size-small;
        margin-bottom: 12px;
        color: rgba(0, 0, 0, 0.4);
      }
      &:first-child{
        border:none;
      }
    }
  }
  &.anonsBlock{
    h1{
      font-size:$font-size-large;
      font-family: $font-family-base;
    }
    h2{
      margin-bottom: 12px;
    }
    p{
      font-family: $font-family-base;
    }
    .side{
      h2{
        font-size:$font-size-large;
        line-height: 1.1;
        margin-top: 0;
        margin-bottom: 8px;
        color:$text-color;
        font-family: $font-family-base;
      }
      h3{
        font-size:$font-size-h4;
        line-height: 1.3;
        color:$text-color;
        font-family: $font-family-base;
      }
      p{
        font-size:$font-size-base;
        line-height: 1.1;
        color:$gray-light;
      }
      a{
        margin-bottom: 20px;
      }
    }
  }
}

