            background-image: url('../img/icons/social/tw-hover.svg');
          }
        }
      }
    }
    .list-group{
      border-top:4px solid $light;
      border-bottom:4px solid $light;
      .list-group-item{
        font-size:$font-size-base;
        border-top:2px solid $light;
        padding-top: 20px;
        padding-bottom: 15px;
        h2{
          font-size:$font-size-base;
          color:$gray-light;
          font-style:italic;
          line-height: 1.1;
          margin-top: 0;
          margin-bottom: 8px;
        }
        a{
          display: block;
          line-height: 1.1;
          margin-bottom: 8px;
        }
        p {
          line-height: 1.3;
          font-size:$font-size-small;
          margin-bottom: 12px;
          color: rgba(0, 0, 0, 0.4);
        }
        &:first-child{
          border:none;
        }
      }
    }
    &.anonsBlock{
      h1{
        font-size:40px;
        line-height: 1.1;
        margin-bottom: 19px;
      }
      h2{
        margin-top: 0;
        margin-bottom: 30px;
        line-height: 35px;
      }
      p{
        font-family: $font-family-base;
      }
      .side{
        h2{
          font-size:32px;
          line-height: 35px;
          margin-top: 0;
          margin-bottom: 30px;
        }
        h3{
          font-size:$font-size-large;
        }
        p{
          font-size:$font-size-h4;
          line-height: 1.3;
          color:$text-color;
        }
        a{
          margin-bottom: 20px;
        }
      }
    }
  }

  .bannerBlock{
    height:400px;
    img{
      height:400px;
    }
    .bannerText{
      padding: 16px 250px 16px 35px;
      h2{
        font-size: 20px;
        line-height: 1.1;
        color: rgba(255, 255, 255, 0.7);
        text-shadow: 0 2px 4px $gray-light;
      }
      p{
        font-size: 2.29em;
        line-height: 1.0;
        color: #ffffff;
        text-shadow: 0 2px 4px $gray-light;
      }
    }
  }
  .pollBlock{
    height:400px;
    padding-left: 20px;
    padding-right: 20px;
    .poll{
      height:255px;
      margin: 0;
    }
    blockquote{
      p{
        margin-top: 85px;
      }
    }
    h2{
      font-size: $font-size-h4;
    }
    p{
      font-size: $font-size-h4;
      line-height: 24px;
    }
  }

  footer{
    padding-top: 26px;
    padding-bottom: 40px;
    .links{
      a{
        line-height: 1.1;
        color: rgba(255, 255, 255, 0.7);
        &:hover{
          color: rgba(255, 255, 255, 0.87);
        }
        &:focus,
        &:active{
          color: rgba(255, 255, 255, 1);
        }
      }
    }
    .geo{
      a{
        color: rgba(26, 156, 255, 1.0);
        &:hover{
          color: rgba(26, 156, 255, 0.87);
        }
        &:focus,
        &:active{
          color: rgba(26, 156, 255, 0.7);
        }
      }
    }
    h4{
      font-weight:bold;
    }
    ul{
      list-style-type: none;
      padding: 0;
      padding-top: 5px;
      margin-bottom: 20px;
      > li{
        margin-bottom: 15px;
        > hr{
          margin: 0;
        }
      }
    }

    .footerSubscr{
      width:180px;
      margin-bottom: 45px;
      .form-control{
        display: inline-block;
        width:125px;
        border: solid 1px rgba(255, 255, 255, 0.1);
        color: rgba(255, 255, 255, 0.3);
      }
    }

    .footerSocial{
      .social{
        a{
          margin: 0px 4px;
        }
      }
    }

    .footInfo{
      margin-top: 20px;
      .block{
        background-position: left top;
        background-size:20px 20px;
        background-repeat:no-repeat;
        padding-left: 35px;
        margin-bottom: 15px;
        &.err{
          background-image:url("../img/icons/ic-error-outline-black-24-px.svg");
        }
        &.copy{
          background-image:url("../img/icons/ic-copyright-black-24-px.svg");
        }
        &.geo{
          background-image:url("../img/icons/ic-pin-drop-black-24-px.svg");
        }
      }
    }

    .footGov{
      a{
        color:inherit;
        display: block;
        margin-top: 20px;
        img{
          margin-bottom: 15px;
          height:90px;
        }
      }
      span{
        display: block;
      }
    }
  }
}


